import portfolioData from "../utils/chartData";

let dataSource = JSON.parse(JSON.stringify(portfolioData));

export const getAllPortfolios = () =>
  dataSource.expectedRiskReturnComparison.riskReturnAnalyticResponse.map(
    (m) => m.portfolioName
  );

const getOption = (dateRange, currency, p1, p2) => {
  dataSource = JSON.parse(JSON.stringify(portfolioData));
  const mulValue = currency === "USD" ? 0.71 : 1;
  const histDataP1 = dataSource.historicalPerformanceComparison.portfolios
    .filter((obj) => obj.portfolioName === p1)[0]
    .portfolioHistoricalPerformance.sort()
    .reverse();
  const histDataP2 = dataSource.historicalPerformanceComparison.portfolios
    .filter((obj) => obj.portfolioName === p2)[0]
    .portfolioHistoricalPerformance.sort()
    .reverse();
  const categories = [];
  const months = [
    "Jan",
    "Feb",
    "Mar",
    "Apr",
    "May",
    "Jun",
    "Jul",
    "Aug",
    "Sept",
    "Oct",
    "Nov",
    "Dec",
  ];
  const data = { p1: [], p2: [] };
  const currentDate = new Date();
  let month =
    dateRange === "1Y"
      ? 12
      : dateRange === "6M"
      ? 6
      : dateRange === "5Y"
      ? 61
      : currentDate.getMonth() + 1;

  if (dateRange === "1Y" || dateRange === "YTD" || dateRange === "6M") {
    while (month) {
      data.p1.push(histDataP1[month - 1].value * mulValue);
      data.p2.push(histDataP2[month - 1].value * mulValue);
      var index = parseInt(histDataP1[month - 1].date.slice(5, 7));
      month--;
      categories.push(months[index - 1]);
    }
  } else if (dateRange === "5Y") {
    while (month) {
      data.p1.push(histDataP1[month - 1].value * mulValue);
      data.p2.push(histDataP2[month - 1].value * mulValue);
      categories.push(histDataP1[month - 1].date);
      month--;
    }
  } else if (dateRange === "Max") {
    histDataP1.sort().reverse();
    histDataP2.sort().reverse();
    histDataP1.forEach((obj) => {
      data.p1.push(obj.value * mulValue);
      data.p2.push(histDataP2[histDataP1.indexOf(obj)].value * mulValue);
      categories.push(obj.date);
    });
  }
  return { categories, data };
};
export default getOption;
