import getOption from "./getOption";

const months = [
  "Jan",
  "Feb",
  "Mar",
  "Apr",
  "May",
  "Jun",
  "Jul",
  "Aug",
  "Sept",
  "Oct",
  "Nov",
  "Dec",
];

export default (options, dateR, cur, p1, p2) => {
  const currentChartOption = getOption(dateR, cur, p1, p2);
  options.tooltip.valueSuffix = " " + cur;
  options.series[0].name = p1;
  options.series[1].name = p2;
  options.series[0].data = currentChartOption.data["p1"];
  options.series[1].data = currentChartOption.data["p2"];
  options.xAxis.categories = currentChartOption.categories;
  if (dateR === "5Y" || dateR === "Max") {
    options.xAxis.tickInterval = 12;
    options.xAxis.labels.formatter = function () {
      return `${
        months[parseInt(this.value.slice(5, 7) - 1)]
      } ${this.value.slice(0, 4)}`;
    };
  } else {
    options.xAxis.tickInterval = 0;
    options.xAxis.labels.formatter = function () {
      return this.value;
    };
  }
  return { ...options };
};
