export default {
  chart: {
    type: "spline",
    backgroundColor: "#214761",
    borderRadius: '15'
  },
  title: {
    text: "Portfolio value based on gross returns",
    style: { color: "#FFFFFF" },
  },
  subtitle: {
    text:
      "Gross returns and exchange rates sourced from Bloomberg as of April 2020",
    style: { color: "#FFFFFF" },
  },
  xAxis: {
    labels: {
      style: {
        color: "#FFFFFF",
      },
    },
    categories: [
      "Jan",
      "Feb",
      "Mar",
      "Apr",
      "May",
      "Jun",
      "Jul",
      "Aug",
      "Sep",
      "Oct",
      "Nov",
      "Dec",
    ],
  },
  yAxis: {
    title: {
      style: {
        color: "#FFFFFF",
      },
      text: "value",
    },
    labels: {
      formatter: function () {
        return this.value;
      },
      style: {
        color: "#FFFFFF",
      },
    },
  },
  tooltip: {
    crosshairs: true,
    shared: true,
  },
  plotOptions: {
    series: {
      dataGrouping: {
        forced: true,
        units: [["month", [12]]],
      },
    },
    spline: {
      marker: {
        radius: 1,
        lineColor: "#666666",
        lineWidth: 1,
      },
    },
  },
  legend: {
    itemStyle: {
      color: "#FFFFFF",
    },
  },
  series: [
    {
      name: "stashAway risk index 14%",
      data: [],
    },
    {
      name: "40% VTMX(stock)",
      data: [],
      color: "#FFFF00",
    },
  ],
};
