import React, { useEffect, useState, useContext } from "react";
import { Select } from "antd";
import { getAllPortfolios } from "../utils/getOption";
import { ChartContext } from "../utils/chartContext";
import loadChart from "../utils/loadChart";
const { Option } = Select;

function Filterpane() {
  const {
    currency,
    filterType,
    option,
    setOption,
    firstPortfolio,
    secondPortfolio,
    setSecondPortfolio,
  } = useContext(ChartContext);
  const [portfolios, setPortfolios] = useState([]);

  useEffect(() => {
    const allPortfolios = getAllPortfolios();
    setPortfolios(allPortfolios.splice(1));
  }, []);

  const onChangeBenchMark = (value) => {
    setSecondPortfolio(value);
    setOption(loadChart(option, filterType, currency, firstPortfolio, value));
  };

  const options = portfolios.map((po) => (
    <Option key={po} value={po}>
      {po}
    </Option>
  ));
  return (
    <React.Fragment>
      <h3 className="paddingLeft25">Portfolio Benchmark</h3>
      <div className="container-fluid">
        <div className="row chartFilterPane">
          <p className="vs">
            <span className="rounded-circle vsSpan">VS</span>
          </p>
          <div className="col-lg-6 leftPane rounded-left">
            <h5>General Investing</h5>
            <h5 style={{ color: "#2879d4" }}>{firstPortfolio}</h5>
          </div>
          <div className="col-lg-6 rounded-right rightPane">
            <Select
              style={{ width: "88%" }}
              defaultActiveFirstOption={true}
              value={secondPortfolio}
              onChange={(value, evt) => {
                onChangeBenchMark(value);
              }}
            >
              {options}
            </Select>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

export default Filterpane;
