import React, { useState, useEffect } from "react";
import FilterPane from "./filterPane";
import Chart from "./chart";
import ChartHeader from "./chartHeader";
import { ChartContext } from "../utils/chartContext";
import { getAllPortfolios } from "../utils/getOption";
import optionData from "../utils/constant";
import loadChart from "../utils/loadChart";

function ChartOverview() {
  const [currency, setCurrency] = useState("SGD");
  const [filterType, setFilterType] = useState("Max");
  const [firstPortfolio, setFirstPortfolio] = useState("");
  const [secondPortfolio, setSecondPortfolio] = useState("");
  const [option, setOption] = useState(optionData);

  useEffect(() => {
    const allPortfolios = getAllPortfolios();
    setFirstPortfolio(allPortfolios[0]);
    setSecondPortfolio(allPortfolios[1]);
    setOption(() =>
      loadChart(optionData, "Max", "SGD", allPortfolios[0], allPortfolios[1])
    );
  }, []);

  return (
    <div>
      <ChartContext.Provider
        value={{
          currency,
          setCurrency,
          filterType,
          setFilterType,
          option,
          setOption,
          firstPortfolio,
          secondPortfolio,
          setSecondPortfolio,
        }}
      >
        <FilterPane></FilterPane>
        <ChartHeader></ChartHeader>
        <Chart></Chart>
      </ChartContext.Provider>
    </div>
  );
}

export default ChartOverview;
