import React, { useContext } from "react";
import { Radio } from "antd";
import { ChartContext } from "../utils/chartContext";
import loadChart from "../utils/loadChart";

function ChartHeader() {
  const {
    currency,
    setCurrency,
    filterType,
    setFilterType,
    option,
    setOption,
    firstPortfolio,
    secondPortfolio,
  } = useContext(ChartContext);

  const onChangeFilterType = ({ target }) => {
    setFilterType(target.value);
    setOption(
      loadChart(option, target.value, currency, firstPortfolio, secondPortfolio)
    );
  };

  const onChangeCurrency = ({ target }) => {
    setCurrency(target.value);
    setOption(
      loadChart(
        option,
        filterType,
        target.value,
        firstPortfolio,
        secondPortfolio
      )
    );
  };

  return (
    <React.Fragment>
      <div className="container-fluid chartHeader">
        <Radio.Group
          buttonStyle="solid"
          onChange={onChangeFilterType}
          value={filterType}
        >
          <Radio.Button value="6M">6 months </Radio.Button>
          <Radio.Button value="YTD">Year-to-date</Radio.Button>
          <Radio.Button value="1Y">1 year</Radio.Button>
          <Radio.Button value="5Y">5 years</Radio.Button>
          <Radio.Button value="Max">Max</Radio.Button>
        </Radio.Group>
        <Radio.Group
          onChange={onChangeCurrency}
          value={currency}
          buttonStyle="solid"
          style={{ float: "right" }}
        >
          <Radio.Button value="SGD">SGD</Radio.Button>
          <Radio.Button value="USD">USD</Radio.Button>
        </Radio.Group>
      </div>
    </React.Fragment>
  );
}

export default ChartHeader;
